.main-menu-wrapper {
  overflow-x: hidden;
  overflow-y: scroll;
  margin: 0;
  position: fixed;
  height: 100%;
  width: 100%;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.main-menu-wrapper::-webkit-scrollbar {
  display: none;
}

@media screen and (orientation: portrait) {
  .main-menu {
    background-image: url("../../assets/mainMenuBackgroundPortrait.png");
    background-size: 100% 100%;
  }
}

@media screen and (orientation: landscape) {
  .main-menu {
    background-image: url("../../assets/mainMenuBackgroundLandscape.png");
    background-size: 100% 100%;
  }
}

.main-menu .color {
  color: #fccec0;
}
